 * animation text-pop-up-top
 * ----------------------------------------
 */
@-webkit-keyframes text-pop-up-top {
	0% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
		-webkit-transform-origin: 50% 50%;
		transform-origin: 50% 50%;
		text-shadow: none;
	}
	100% {
		-webkit-transform: translateY(-5px);
		transform: translateY(-5px);
		-webkit-transform-origin: 50% 50%;
		transform-origin: 50% 50%;
		text-shadow: 0 0.5px 0 $shadowColor, 0 1px 0 $shadowColor,
			0 1.5px 0 $shadowColor, 0 2px 0 $shadowColor, 0 2.5px 0 $shadowColor,
			0 3px 0 $shadowColor, 0 3.5px 0 $shadowColor, 0 4px 0 $shadowColor,
			0 4.5px 0 $shadowColor, 0 20px 10px rgba(0, 0, 0, 0.3);
	}
}
